import { Component, ChangeDetectionStrategy, Input } from '@angular/core';

@Component({
  selector: 'bb-loan-summary-amount-item',
  templateUrl: './loan-summary-amount-item.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanSummaryAmountItemComponent {
  /**
   * Item title
   */
  @Input() title!: string;
  /**
   * Item amount
   */
  @Input() amount: string | undefined;
  /**
   * Amount currency
   */
  @Input() currency: string | undefined;
  /**
   * Whether currency local should be transformed to symbol
   */
  @Input() isCurrencySymbolUsed: boolean | undefined;
  /**
   * Whether amount will be as percentage
   */
  @Input() showPercent = false;
  /**
   * Title elem classes
   */
  @Input() titleClasses!: string | string[];
  /**
   * Amount elem classes
   */
  @Input() amountClasses!: string | string[];
  /**
   * Additional label with total amount.
   */
  @Input() totalAmount: string | undefined;
  /**
   * Whether label with total amount should be shown.
   */
  @Input() showLabel: boolean | undefined;
}
